
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import {
jsonpReturn, //处理返回的数据
checkRequest
}
from '@/libs/util'


import {
SendMessage, //发送短信验证码
Reset
}
from '@/api/user'

export default {
data() {
return {
logo: require('@/assets/logo.png'),
alipay: require('@/assets/alipay.png'),
wechatpay: require('@/assets/wechatpay.png'),
value: '',
form: {
tel: '',
tel_code: '', //验证码
password: ''
},
intervalTime: 60, //计时器倒计时时间
interval: false, //计时器开启
toastText: '', // 弹出框
toast: false
}
},
components: {
Group,
XInput,
XButton,
Divider,
Flexbox,
FlexboxItem,
Toast
},
methods: {
link() {
this.$router.push('password')
},
countDown(time) {
let check = this.$refs.tel.valid
console.log(check);
if (check) {
this.interval = true
SendMessage(this.form.tel).then((res) => {
if (checkRequest(res, false)) {
this.countDownInterval() 
}
})
} else {
this.toast = true
this.toastText = '请填写正确手机号'
}
},
countDownInterval() { //发送验证码
const Int = setInterval(() => {
this.intervalTime--
if (this.intervalTime == 0) {
clearInterval(Int)
this.interval = false
this.intervalTime = 60
}
}, 1000)
},
submit() { //手机注册
let check = this.$refs.tel.valid && this.$refs.tel_code.valid && this.$refs.password.valid
console.log(check);
if (check) {
Reset(
this.form.tel,
this.form.tel_code,
this.form.password
).then((res) => {
this.toast = true
this.toastText = checkRequest(res)
})
} else {
this.toast = true
this.toastText = '请确保表单信息正确'
}

}
}
}
